import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

//state를 이용하기위해서 class component를 사용합니다.
 class App extends React.Component {
    state = {
      isLoading: true,
      movies: []
    };
    getMovies = async () => {
      const {
        data: {
          data: { movies }
        }
      } = await axios.get(//ES6에서의 기능으로 오브젝트 안에 있는 값을 불러올 수 있습니다.
      "https://yts-proxy.now.sh/list_movies.json?sort_by=rating"
      );
      this.setState({ movies, isLoading: false });
    };
    componentDidMount() {
      this.getMovies();
    }
    render() {
      const { isLoading, movies } = this.state;
      return (
            <section className="container">
            {isLoading ? (
              <div className="loader">
                <span className="loader__text">Loading...</span>
              </div>
            ) : (
              <div className="movies">
                {movies.map(movie => (
                  <Movie
                    key={movie.id}
                    id={movie.id}
                    year={movie.year}
                    title={movie.title}
                    summary={movie.summary}
                    poster={movie.medium_cover_image}//위와 같이 object를 풀어줄 때 map함수를 사용하고, 또, jsx에서는 props를 통해서 값을 전달합니다. 
                //key는 표현되지 않지만 필수props입니다.
                    genres={movie.genres}
                />
                ))}
              </div>
        )}
      </section>
    );
  }
}
export default App;